<template>

  <form>
                <v-card
    class="mx-auto"
    max-width="344"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
           Configuración
        </div>
        <v-list-item-title class="text-h5 mb-1">
          Perfil pasajero
        </v-list-item-title>

        <v-list-item-subtitle>    <v-text-field
      v-model="name"
      :error-messages="nameErrors"
      :counter="20"
      label="Name"
      required
      @input="$v.name.$touch()"
      @blur="$v.name.$touch()"
    ></v-text-field></v-list-item-subtitle>
        <v-list-item-subtitle>
    <v-text-field
      v-model="email"
      :error-messages="emailErrors"
      label="E-mail"
      required
      @input="$v.email.$touch()"
      @blur="$v.email.$touch()"
    ></v-text-field></v-list-item-subtitle>
        <v-list-item-subtitle>    <v-text-field
      v-model="password"
      label="Password"
      required
    ></v-text-field></v-list-item-subtitle>
        <v-list-item-subtitle>    <v-text-field
      v-model="telefono"
      label="Telefono"
      required
      type="number"
    ></v-text-field></v-list-item-subtitle>
        <v-list-item-subtitle>    <v-text-field
      v-model="numeroDocumentoIdentidad"
      label="Documento identidad"
      required
    ></v-text-field></v-list-item-subtitle>
      
   
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="80"
        color="grey"
      ></v-list-item-avatar>
    </v-list-item>

    <v-card-actions>
      <v-flex text-xs-right>
      <v-btn
        outlined
        rounded
        text
         @click="submit()" 
      >
        Enviar
      </v-btn>
      </v-flex>
    </v-card-actions>
  </v-card>
  </form>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { required, maxLength, email } from 'vuelidate/lib/validators'
  import axios from 'axios'
  export default {
    mixins: [validationMixin],

    validations: {
      name: { required, maxLength: maxLength(20) },
      email: { required, email }
    },

    data: () => ({
      name: '',
      email: '',
      password: '',
      telefono: '',
      numeroDocumentoIdentidad: '',
      objeto: '',
      
    }),

    computed: {
  
      nameErrors () {
        const errors = []
        if (!this.$v.name.$dirty) return errors
        !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long')
        !this.$v.name.required && errors.push('Name is required.')
        return errors
      },
      emailErrors () {
        const errors = []
        if (!this.$v.email.$dirty) return errors
        !this.$v.email.email && errors.push('Must be valid e-mail')
        !this.$v.email.required && errors.push('E-mail is required')
        return errors
      }
    },

    methods: {
      submit () {
        this.$v.$touch()
         if (this.$v.$invalid) {
        console.log("error completa los campos")
      }
      else{
            var payload = {nombre:this.name, correo: this.email, contrasena : this.password, telefono : this.telefono, numeroDocumentoIdentidad : this.numeroDocumentoIdentidad};
      axios({
        url: "usuarioaplicacion/ajusteperfil/"+this.$store.state.theUser,
        data: payload,
        method: "PUT",
      })
        .then(() => {
    
        })
        .catch((err) => {
          console.log(err);
        });
      }
      },
      clear () {
        this.$v.$reset()
        this.name = ''
        this.email = ''
        this.password = ''
   
      },
        initialize () {
              axios({
        url: "usuarioaplicacion/"+this.$store.state.theUser,
        method: "GET",
      })
        .then((resp) => {
            console.log(resp.data)
      this.objeto = resp.data.usuarioAplicacion;
      this.name = this.objeto.nombre;
      this.email = this.objeto.correo;
      this.password = this.objeto.contrasena;
      this.telefono = this.objeto.telefono;
      this.numeroDocumentoIdentidad = this.objeto.numeroDocumentoIdentidad;
      
    })
        .catch((err) => {
          console.log(err);
        });
        }
  
      },
  created() {
    this.initialize();
  }
  }
</script>

<style scoped>

h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>