<template>
  <v-layout align-center justify-center>
    <v-card
    class="mx-auto"
    max-width="344"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
           Viaje cancelado
        </div>
        <v-list-item-title class="text-h5 mb-1">
          ¿Cómo te sentiste?
        </v-list-item-title>

        <v-list-item-subtitle>         <v-text-field
              label="Mi experiencia"
            placeholder="Feedback"
            v-model="feedback"
          ></v-text-field></v-list-item-subtitle>
   
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="80"
        color="grey"
      ></v-list-item-avatar>
    </v-list-item>

    <v-card-actions>
      <v-flex text-xs-right>
      <v-btn
        outlined
        rounded
        text
         @click="enviarFeedback()" 
      >
        Enviar
      </v-btn>
      </v-flex>
    </v-card-actions>
  </v-card>
  </v-layout>
  <!--   </v-app>  -->
</template>

<script>
import axios from "axios";

export default {
  name: "conductorViajeCancelado",
  props: {},
  data() {
    return {
      conductor: "Viaje cancelado",
      theUser: "",
      feedback: "",
    };
  },
  methods: {
    enviarFeedback: function () {
      //console.log(this.$store.state.theUser);
      var payload = { feedbackConductor: this.feedback, idHistorialRuta: this.$store.state.idHistorial, estadoViaje: "3"};
      axios({
        url: "historialruta/conductor/feedback",
        data: payload,
        method: "POST",
      })
        .then(() => {
        this.$store.dispatch("guardarViajePendiente", 0);

      this.$router.push("/buscarpasajero");

          
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {},
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
