<template>
  <v-app>
        <v-layout align-center justify-center>
          <v-card>
    <v-data-table
      :headers="headers"
      :items="pasajerosDisponibles"
      :search="search"
      sort-by="idPasajero.nombre"
      class="elevation-1"
    >
      <!-- <template v-slot:item.actions="{ item }"> -->
      <template v-slot:[`item.actions`]="{ item }">
        <template>
          <v-btn @click="terminarViaje(item._id)">Aceptar</v-btn>
        </template>
       
      </template>

      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Reset </v-btn>
      </template>
    </v-data-table>
    <!-- --------------------------------------- -->
    <!--  <v-simple-table v-if="this.$store.state.viajePendiente == 0">
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">Nombre</th>
            <th class="text-left">Direccion origen</th>
            <th class="text-left">Direccion destino</th>
            <th class="text-left">Dinero ofertado</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in pasajerosDisponibles" :key="item.name">
            <td>{{ item.idPasajero.nombre }}</td>
            <td>{{ item.lugarOrigen }}</td>
            <td>{{ item.lugarDestino }}</td>
            <td>{{ item.dineroPagado }}</td>
            <v-btn @click="aceptarViajeidConductor(item._id)">Aceptar</v-btn>
          </tr>
        </tbody>
      </template>
    </v-simple-table> -->
          </v-card>
        </v-layout>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  name: "pasajeroViaje",
  props: {},

  data() {
    return {
      conductor: "Esperando lista pasajeros...",
      theUser: "",
      pasajerosDisponibles: [],
      timer: null,
      recarga: 1,
      search:"",
      headers: [
        {
          text: "Nombre",
          value: "idPasajero.nombre",
          align: "start",
          sortable: true,
        },
        {
          text: "Direccion origen",
          value: "lugarOrigen",
        },
        {
          text: "Direccion destino",
          value: "lugarDestino",
        },
        {
          text: "Dinero ofertado",
          value: "dineroPagado",
        },
        {
          text: "Acciones",
          value: "actions",
          sortable: false,
        },
      ],
    };
  },
  computed: {
    filteredStudents() {
      return this.uStudents.filter((user) => {
        const filter = this.filter.toUpperCase();
        const hasIdMatch = String(user.student_id).includes(filter);
        const hasNameMatch = user.student_name.toUpperCase().includes(filter);

        return hasIdMatch || hasNameMatch;
      });
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    aceptarViajeidConductor: function (varHistorialId) {
      console.log("Imprimiendo id usuario");
      console.log(this.$store.state.theUser);
      var payload = {
        idConductor: this.$store.state.theUser,
        historialId: varHistorialId,
      };
      axios({
        url: "/historialruta/conductoraceptaviaje",
        data: payload,
        method: "POST",
      })
        .then((resp) => {
          console.log(resp.data);
          this.$store.dispatch(
            "guardarIdHistorial",
            resp.data.historialListo._id
          );
          this.$router.push("/conductorenviaje");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getUnassignedStudents() {
      axios
        .get(`/api/internship/unassignedStudents/${this.$route.params.id}`)
        .then((response) => (this.uStudents = response.data));
    },
    initialize() {
      this.pasajerosDisponibles = [];
      axios({
        url: "/historialruta/buscarpasajero",
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      })
        .then((resp) => {
          console.log(resp.data.historial);
          this.pasajerosDisponibles = resp.data.historial;
          console.log("Imprimiendo" + this.pasajerosDisponibles);
        })
        .catch((err) => {
          console.log(err);
        });
      /////
      var payload = { idUsuario: this.$store.state.theUser };
      axios({
        url: "historialruta/viajependiente",
        data: payload,
        method: "POST",
      })
        .then((resp) => {
          console.log(
            "imprimiendo viaje pendiente en store " +
              this.$store.state.viajePendiente
          );
          if (resp.data.historial[0] == null) {
            this.$store.dispatch("guardarViajePendiente", 0);
          } else {
            this.$store.dispatch(
              "guardarViajePendiente",
              resp.data.historial[0]._id
            );
          }
          this.$store.dispatch(
            "guardarIdHistorial",
            resp.data.historial[0]._id
          );
          console.log(this.$store.state.theUser);
        })
        .catch((err) => {
          console.log(err);
        });
    },

    viajeFinalizado: function () {
      this.$router.push("conductor/viajefinalizado");
    },
    cancelarViaje: function () {
      this.$router.push("conductor/viajecancelado");
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      if (this.recarga == 1) {
        this.initialize();
      }
    }, 15000);
  },

  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
