<template>
 <v-app>
      <v-layout align-center justify-center>
          <v-card>
            <v-data-table
              :headers="headers"
              :items="historial"
              :search="search"
              sort-by="estadoViaje"
              class="elevation-1"
            >
              <template v-slot:[`item.estadoViaje`]="{ item }">
                <div v-if="item.estadoViaje == '0'">
                  <span class="yellow--text">Solicitado</span>
                </div>
                <div v-else-if="item.estadoViaje === '1'">
                  <span class="blue--text">En proceso</span>
                </div>
                <div v-else-if="item.estadoViaje === '2'">
                  <span class="green--text">Finalizado</span>
                </div>
                <div v-else-if="item.estadoViaje === '3'">
                  <span class="red--text">Cancelado</span>
                </div>
              </template>
              <!-- <template v-slot:item.actions="{ item }"> -->
              <template v-slot:[`item.actions`]="{ item }">
                <template v-if="item.estadoViaje === '1'">
                  <v-btn @click="terminarViaje(item._id)"
                    >Terminar</v-btn
                  >
                </template>
                <template v-else> </template>
              </template>

              <template v-slot:no-data>
                <v-btn color="primary" @click="initialize"> Reset </v-btn>
              </template>
            </v-data-table>
          </v-card>
      </v-layout>

  </v-app>
</template>
<script>
import axios from "axios";

export default {
  name: "historialPasajero",
  props: {},
  data() {
    return {
      theUser: "",
      historial: [],
      headers: [
        {
          text: "Fecha",
          value: "createAt",
          align: "start",
          sortable: true,
        },
        {
          text: "Nombre Conductor",
          value: "idConductor.nombre",
        },
        {
          text: "Lugar origen",
          value: "lugarOrigen",
        },
        {
          text: "Lugar destino",
          value: "lugarDestino",
        },
        {
          text: "Dinero pagado",
          value: "dineroPagado",
        },
        {
          text: "Estado",
          value: "estadoViaje",
        },
        {
          text: "Acciones",
          value: "actions",
          sortable: false,
        },
      ],
    };
  },
  created() {
    this.initialize();
  },
  methods: {
    initialize() {
      //console.log(this.$store.state.theUser);
      var payload = { idPasajero: this.$store.state.theUser };
      axios({
        url: "/historialruta/historialpasajero",
        method: "POST",
        data: payload,
      })
        .then((resp) => {
          console.log(resp.data);
          this.historial = resp.data.historial;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    terminarViaje(id){
           this.$store.dispatch("guardarIdHistorial", id);
            this.$router.push("/buscarconductor");

      
    }
  },
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
