<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 xl4>
      <v-card
    class="mx-auto"
    max-width="344"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="text-overline mb-4">
          Viaje en proceso
        </div>
        <v-list-item-title class="text-h5 mb-1">
          {{datosUsuario.idPasajero.nombre}} 
        </v-list-item-title>
        <v-list-item-subtitle>Correo: {{ datosUsuario.idPasajero.correo }}</v-list-item-subtitle>
        <v-list-item-subtitle>Teléfono: {{ datosUsuario.idPasajero.telefono }}</v-list-item-subtitle>
        <v-list-item-subtitle>Documento identidad: {{ datosUsuario.idPasajero.numeroDocumentoIdentidad }}</v-list-item-subtitle>
      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="80"
        color="grey"
      ></v-list-item-avatar>
    </v-list-item>

    <v-card-actions>
      <v-btn
        outlined
        rounded
        text
         @click="cancelarViaje()"
      >
        Cancelar
      </v-btn>
      <v-flex text-xs-right>
      <v-btn
        outlined
        rounded
        text
         @click="viajeFinalizado()" 
      >
        Viaje finalizado
      </v-btn>
      </v-flex>
    </v-card-actions>
  </v-card>
    </v-flex>
  </v-layout>
  <!--   </v-app>  -->
</template>

<script>
import axios from "axios";

export default {
  name: "pasajeroViaje",
  props: {},
  data() {
    return {
      conductor: "Viaje aceptado, apresúrate",
      theUser: "",
      datosUsuario: [],
      timer: null,
      recarga : 1
    };
  },
  methods: {
    refrescar: function () {
      var payload = { historialId: this.$store.state.idHistorial };
      axios({
        url: "historialruta/refrescarpasajero",
        data: payload,
        method: "POST",
      })
        .then((resp) => {
          console.log(resp.data);
          this.datosUsuario = resp.data.historial;
          this.$store.dispatch("guardarIdHistorial", resp.data.historial._id);
          /* if(this.datosUsuario){this.recarga = 0}
          else{this.recarga =1} */
          if(this.datosUsuario.estadoViaje == 2){
            this.$router.push("conductor/viajefinalizado");
          }
          else if(this.datosUsuario.estadoViaje == 3){
            this.$router.push("conductor/viajecancelado");
          }
})
        .catch((err) => {
          console.log(err);
        });
    },
    viajeFinalizado: function () {
      this.$router.push("conductor/viajefinalizado");
    },
    cancelarViaje: function () {
      this.$router.push("conductor/viajecancelado");
    },
  },
  mounted() {
    this.timer = setInterval(() => {
      if (this.recarga == 1) {
        this.refrescar();
      }
    }, 4000);
  },

   beforeDestroy() {
    clearInterval(this.timer);
  }, 
};
</script>

<style scoped>
h3 {
  margin: 0px 0 0;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>
